nav, #main {
  font-family: sans-serif;
  max-width: 80ch;
  margin: 0px auto;
}

h1, h2, h3 {
  font-family: serif;
}

table {
  width: 100%;
  border-collapse: collapse;
}

nav img {
  vertical-align: middle;
}

nav a {
  vertical-align: middle;
}

nav table {
  border-collapse: reset;
}

.p-summary {
  font-size: 80%;
  font-weight: lighter;
  margin-top: 0;
  margin-left: 2em;
}

.p-name {
  margin-bottom: 0;
}

.linenum {
  user-select: none;
  text-align: right;
  border-right: 1px solid;
  padding-left: 5px;
  padding-right: 5px;
}

.line-content, .readme {
  padding-left: 5px;
  padding-right: 5px;
}

code {
  background: #eee;
  background-color: #eee;
}

code tr {
  height: 17px;
}

pre code {
  display: block;
  overflow-x: auto;
  border: 1px solid black;
  border-radius: 5px;
  background: #ffd;
  background-color: #ffd;
}

h1 code, h2 code, h3 code, p code {
  display: inline;
}

blockquote {
  padding: 10px 20px;
  border-left: 5px solid #eee;
}

.subtle {
  color: #000;
  text-decoration: none;
}

.subtle:hover {
  text-decoration: underline;
}
